import { fakeChartData } from 'services/api';
const state = {
    chartData: {
        visitData: [],
        visitData2: [],
        salesData: [],
        searchData: [],
        offlineData: [],
        offlineChartData: [],
        salesTypeData: [],
        salesTypeDataOnline: [],
        salesTypeDataOffline: [],
        radarData: []
    },
    loading: false
};
const mutations = {
    save(state, payload) {
        state.chartData = Object.assign({}, state.chartData, payload);
    },
    setLoading(state, loading) {
        state.loading = loading;
    }
};
const actions = {
    async fetch(context) {
        try {
            context.commit('setLoading', true);
            const response = await fakeChartData();
            const data = response.data;
            context.commit('setLoading', false);
            context.commit('save', data);
            return data;
        }
        catch (error) {
            context.commit('setLoading', false);
            throw error;
        }
    },
    async fetchSalesData(context) {
        try {
            context.commit('setLoading', true);
            const response = await fakeChartData();
            const data = response.data;
            context.commit('setLoading', false);
            context.commit('save', {
                salesData: data.salesData
            });
            return data;
        }
        catch (error) {
            context.commit('setLoading', false);
            throw error;
        }
    }
};
export default {
    namespaced: true,
    state,
    mutations,
    actions
};



