import { queryTags } from 'services/api';
const state = {
    tags: []
};
const mutations = {
    saveTags(state, tags) {
        state.tags = tags;
    }
};
const actions = {
    async fetchTags(context) {
        try {
            const response = await queryTags();
            const data = response.data.list || [];
            context.commit('saveTags', data);
            return data;
        }
        catch (error) {
            throw error;
        }
    }
};
export default {
    namespaced: true,
    state,
    mutations,
    actions
};



