import { queryRule, addRule, removeRule } from 'services/api';
const state = {
    list: [],
    pagination: {}
};
const mutations = {
    save(state, payload) {
        state.list = payload.list || [];
        state.pagination = payload.pagination || {};
    }
};
const actions = {
    async fetch(context, payload) {
        try {
            const response = await queryRule(payload);
            const data = response.data;
            context.commit('save', data);
            return data;
        }
        catch (error) {
            throw error;
        }
    },
    async add(context, payload) {
        try {
            const response = await addRule(payload);
            const data = response.data;
            context.commit('save', data);
            return data;
        }
        catch (error) {
            throw error;
        }
    },
    async remove(context, payload) {
        try {
            const response = await removeRule(payload);
            const data = response.data;
            context.commit('save', data);
            return data;
        }
        catch (error) {
            throw error;
        }
    }
};
export default {
    namespaced: true,
    state,
    mutations,
    actions
};



